*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

button {
  padding: 0;
  border: none;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;

  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  line-height: 1.33;
  letter-spacing: 0;
  font-weight: 500;
}

main {
  flex-grow: 1;
}
