.spinner {
  width: 10px;
  height: 10px;
  margin-left: 10px;
  display: inline-block;
  position: static;
}

.header-library__button .spinner {
  height: 15px;
  margin-left: 5px;
}

.spinner--home {
  width: 30px;
  height: 30px;
  margin-left: 0;
  display: inline-block;
  position: absolute;
  top: 75px;
  left: 50%;
  transform: translate(-50%);
}

.spinner:after {
  content: " ";
  width: 0;
  height: 0;
  box-sizing: border-box;
  border: 15px solid #945e5e;
  border-color: #945e5e rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: auto;
  animation: spinner 1.2s infinite;
  display: block;
}

.header-library__button .spinner:after {
  border: 10px solid #fff;
  border-color: #fff rgba(0, 0, 0, 0);
  animation: spinner 1.2s infinite;
}

@keyframes spinner {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(.55, .055, .675, .19);
  }

  50% {
    animation-timing-function: cubic-bezier(.215, .61, .355, 1);
    transform: rotate(900deg);
  }

  100% {
    transform: rotate(1800deg);
  }
}

.heart {
  width: 24px;
  height: 11px;
  display: inline-block;
  position: relative;
  transform: rotate(45deg);
}

.heart div {
  width: 7px;
  height: 7px;
  background: #ff6b01;
  animation: heart 1.2s cubic-bezier(.215, .61, .355, 1) infinite;
  position: relative;
  top: 2px;
  left: 10px;
}

.heart div:after, .heart div:before {
  content: " ";
  width: 7px;
  height: 7px;
  background: #ff6b01;
  display: block;
  position: absolute;
}

.heart div:before {
  border-radius: 50% 0 0 50%;
  left: -6px;
}

.heart div:after {
  border-radius: 50% 50% 0 0;
  top: -6px;
}

@keyframes heart {
  0% {
    transform: scale(.95);
  }

  5% {
    transform: scale(1.1);
  }

  39% {
    transform: scale(.85);
  }

  45% {
    transform: scale(1);
  }

  60% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

